/*
 * Alerts placement
 */
.alert {
  outline: none;
}
.alert.top,
.alert.top-left,
.alert.top-right,
.alert.bottom,
.alert.bottom-left,
.alert.bottom-right {
  position: fixed;
  z-index: 1050;
  margin: 20px;
}
.alert.top,
.alert.top-left,
.alert.top-right {
  top: 50px;
}
.alert.top {
  right: 0px;
  left: 0px;
}
.alert.top-right {
  right: 0px;
}
.alert.top-right .close {
  padding-left: 10px;
}
.alert.top-left {
  left: 0px;
}
.alert.top-left .close {
  padding-right: 10px;
}
.alert.bottom,
.alert.bottom-right,
.alert.bottom-left {
  bottom: 0px;
}
.alert.bottom {
  right: 0px;
  left: 0px;
}
.alert.bottom-right {
  right: 0px;
}
.alert.bottom-right .close {
  padding-left: 10px;
}
.alert.bottom-left {
  left: 0px;
}
.alert.bottom-left .close {
  padding-right: 10px;
}
